.experience-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.company-logo {
  width: 100px;
  height: 100px;
  margin-right: 20px;
}

.company-logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.experience-details {
  flex: 1;
}

img {
  width: 80px; /* Adjust the desired width */
  height: auto; /* Maintain the aspect ratio */
  border-radius: 50%; /* Make the image circular */
  object-fit: cover; /* Ensure the image covers the container */
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  text-align: center; /* Center the list items */
}

li {
  margin-bottom: 5px;
  font-size: 1rem;
  color: #666666;
  display: inline-block;
  margin-right: 10px;
}

